<template>
    <el-main>
       <p>{{$route.query.user_id}}</p>
      <el-table :data="tableData">
        <el-table-column prop="address_id" label="ID" width="140">
        </el-table-column>
         <el-table-column prop="consignee" label="收货人">
        </el-table-column>
        <el-table-column prop="mobile" label="手机号">
        </el-table-column>
         <el-table-column prop="province" label="省">
        </el-table-column>
         <el-table-column prop="city" label="市">
        </el-table-column>
         <el-table-column prop="district" label="区">
        </el-table-column>
         <el-table-column prop="address" label="详细地址">
        </el-table-column>
      </el-table>

      <el-pagination
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-count="pageCount"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      :total="tableNum">
    </el-pagination>
    </el-main>
    
</template>


<script>
  export default {
       data() {
          return {       
            tableData:[],
            currentPage: 1,
            tableNum:1,
            pageCount:1,
            pageSize:1,
          }

        },
        //初始加载
       created(){
           this.shoplist()
       },
       methods:{
         //商品列表
          shoplist(page=1) {
               var _this=this;
           this.$axios({
             method:'post',
             url:this.url()+'/index.php/useraddress/list',
             params :{
               page:page,
               user_id:_this.$route.query.user_id
             }
           
           }).then(res=>{
              
              console.log(res);
              this.tableData=res.data.data.list;
              this.currentPage=res.data.data.paginate.current_page;
              this.tableNum=res.data.data.paginate.record_count;
              this.pageSize=res.data.data.paginate.page_size;
              this.pageCount=res.data.data.paginate.page_count;
              //console.log(res.data.data.paginate);   

            });     
         },
         
          //分页
          handleCurrentChange(val) {
            this.shoplist(val);
            //console.log(`当前页: ${val}`);
          }
       
      }


  }
</script>

<style>
 
  .el-row {
    margin-bottom: 20px;
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>


